<template>

    <div>
    
        <section class="todoapp">
        
            <el-form ref="form" :model="form">
                <el-input type="hidden" v-model="selectedTable.id"></el-input>
                <header class="header">
                    <h1>Table Designer</h1>
                    <el-form-item label="Table name" prop="name">
                        <el-input v-model="selectedTable.name"></el-input>
                    </el-form-item>
                    <el-form-item label="Description" prop="desc">
                        <el-input type="textarea" v-model="selectedTable.description"></el-input>
                    </el-form-item>
                </header>
                
                <div class="content">
                    <el-form-item>
                        <!-- <table-col class="new-cols"
                         autofocus
                         v-model="newCol"
                         @keyup.enter="addCol" /> -->
                        <table>
                            <thead>
                                <th>NAME</th><th>LABEL</th><th>TYPE</th><th>OPTIONS</th><th>NULLABLE</th><th>PREVIEW</th><th></th>
                            </thead>
                            <tbody>
                                
                                <tr class="new-col">
                                    <td>
                                        <el-input v-model="newCol.name" autofocus ></el-input>
                                        <el-input type="hidden" v-model="newCol.id"></el-input>
                                    </td>
                                    <td>
                                        <el-input v-model="newCol.label"></el-input>
                                    </td>
                                    <td>
                                        <el-select v-model="newCol.type" placeholder="Select the field type">
                                            <el-option label="Text zone" value="text"></el-option>
                                            <el-option label="Checkboxe (for many options with multi choices)" value="checkbox"></el-option>
                                            <el-option label="Text zone (for big text content)" value="textarea"></el-option>
                                            <el-option label="Radio button (for many options with single choice. Up to 3 options)" value="radio"></el-option>
                                            <el-option label="Select box (for many options with single choice. More than 3 options)" value="select"></el-option>
                                            <el-option label="Date (Calendar)" value="date"></el-option>
                                            <el-option label="Number" value="number"></el-option>
                                        </el-select>
                                    </td>
                                    <td>
                                        <el-input v-model="newCol.options"></el-input>
                                    </td>
                                    <td>
                                        <el-switch v-model="newCol.nullable"></el-switch>
                                    </td>
                                    <td>
                                        <transition name="fade">
                                            <el-date-picker
                                                v-if="newCol.type === 'date'"
                                                type="date"
                                                placeholder="Pick a day">
                                            </el-date-picker>
                                            <el-radio v-else-if="newCol.type === 'radio'" v-for="item in newCol.options.split(';')"
                                                :key="item.substring(0, item.indexOf(':'))"
                                                :label="item.substring(item.indexOf(':')+1)"
                                                >
                                                {{ item.substring(0, item.indexOf(':')) }}
                                            </el-radio>
                                            <el-checkbox-group v-else-if="newCol.type === 'checkbox'">
                                                <el-checkbox
                                                    v-for="item in newCol.options.split(';')"
                                                    :key="item.substring(0, item.indexOf(':'))"
                                                    :label="item.substring(item.indexOf(':')+1)">
                                                    {{ item.substring(0, item.indexOf(':')) }}
                                                </el-checkbox>
                                            </el-checkbox-group>
                                            <el-select v-else-if="newCol.type === 'select'" placeholder="Select">
                                                <el-option
                                                    v-for="item in newCol.options.split(';')"
                                                    :key="item.substring(0, item.indexOf(':'))"
                                                    :label="item.substring(item.indexOf(':')+1)"
                                                    :value="item.substring(0, item.indexOf(':'))">
                                                </el-option>
                                            </el-select>
                                            <el-input-number v-else-if="newCol.type === 'number'" @change="handleChange" :min="1" :max="10000"></el-input-number>
                                            <el-input v-else-if="newCol.type === 'textarea'" type="textarea"></el-input>
                                            <el-input v-else="newCol.type === 'text'" placeholder="Please input"></el-input>
                                        </transition>
                                    </td>
                                    <td>
                                        <el-button
                                            @click="addCol"
                                            type="primary"
                                            size="medium">
                                            Add
                                        </el-button>
                                    </td>
                                </tr>
                                
                                
                                
                                <!-- <table-col class="new-cols"
                                 :name="newCol.name"
                                 :label="newCol.label"
                                 :type="newCol.type"
                                 :options="newCol.options"
                                 :nullable="newCol.nullable"
                                 :disable="newCol.disable"
                                 v-on:add="addCol"
                                 @keyup.enter="addCol" /> -->
                                <tr
                                    is="table-col"
                                    v-for="(col, index) in selectedTable.column_collection"
                                    v-bind:column="col"
                                    v-bind:key="col.id"
                                    v-on:remove="removeCol(col)"
                                    v-on:edit="editCol(col)"
                                    v-on:doneEdit="doneEditCol(col)"
                                    v-on:cancelEdit="cancelEditCol(col)"
                                    v-col-focus="col == editedCol"
                                    @dblclick="editCol(col)"
                                    @blur="doneEditCol(col)"
                                    @keyup.enter="doneEditCol(col)"
                                    @keyup.esc="cancelEditCol(col)"
                                    >
                                </tr>
                            </tbody>
                            <tfoot>
                                <tr>
                                    <td colspan="5"></td>
                                    <td colspan="2">
                                        <el-button
                                            @click="saveTable"
                                            type="primary"
                                            size="medium">
                                            Save
                                        </el-button>
                                    </td>
                                </tr>
                            </tfoot>
                        </table>
                    </el-form-item>
                    <!--
                     <el-form-item>
                     <el-button type="primary" @click="submitForm('ruleForm')">Create</el-button>
                     <el-button @click="resetForm('ruleForm')">Reset</el-button>
                     </el-form-item>
                     -->
                </div>
                
            </el-form>
            
        </section>
        
    </div>
    
</template>

<script>

import tableCol from './tableCol'

    export default {
        data () {
            return {
                selectedTable: {
                    id: null,
                    name: '',
                    description: '',
                    column_collection: []
                },
                newCol: {
                    name: '',
                    label: '',
                    type: 'text',
                    options: '',
                    nullable: true,
                    disable: false
                },
                // editedCol: null,
                beforeEditCache: null,
                deletedCol: [],
                loading: true
            }
        },
        created () {
            // récupérer les données lorsque la vue est créée et
            // que les données sont déjà observées
            // this.fetchData()
            // this.selectedTable.id = this.$store.getters.activatedTable
            // this.selectedTable.name = this.$store.getters.activatedTable.name
            // this.selectedTable.description = this.$store.getters.activatedTable.description
            // this.selectedTable.column_collection = this.$store.getters.activatedTable.column_collection
            
            loading = false
        },
        /* watch: {
            // appeler encore la méthode si la route change
            '$route': 'fetchData'
        }, */
        components: {
            tableCol
        },
        // methods that implement data logic.
        // note there's no DOM manipulation here at all.
        methods: {
        
            addCol: function () {
                // var col = this.newCol
                // return
                // alert(JSON.stringify(this.newCol))

                /*
                
                this.newCol.name = (this.newCol.name === typeof("string") ? this.newCol.name.trim() : '')
                this.newCol.label =  (this.newCol.label === typeof("string") ? this.newCol.label.trim() : '')
                this.newCol.type = (this.newCol.type === typeof("string") ? this.newCol.type.trim() : 'text')
                this.newCol.options = (this.newCol.options === typeof("string") ? this.newCol.options.trim() : '')
                this.newCol.nullable = (this.newCol.nullable === typeof(true) ? this.newCol.nullable : true)
                
                
                if (this.newCol.name === '' && this.newCol.label === '') {
                    return
                }
                
                this.column_collection.push({
                    id: undefined,
                    name: (this.newCol.name === typeof("string") ? this.newCol.name.trim() : ''),
                    label: (this.newCol.label === typeof("string") ? this.newCol.label.trim() : ''),
                    type: (this.newCol.type === typeof("string") ? this.newCol.type.trim() : 'text'),
                    options: (this.newCol.options === typeof("string") ? this.newCol.options.trim() : ''),
                    nullable: (this.newCol.nullable === typeof(true) ? this.newCol.nullable : true),
                    disable: true
                })
                
                */
                
                this.selectedTable.column_collection.push({
                    id: undefined,
                    name: this.newCol.name.trim(),
                    label: this.newCol.label.trim(),
                    type: this.newCol.type.trim(),
                    options: this.newCol.options.trim(),
                    nullable: this.newCol.nullable,
                    disable: true
                })
                this.newCol = {
                    name: '',
                    label: '',
                    type: 'text',
                    options: '',
                    nullable: true,
                    disable: true
                }
            },

            removeCol: function (col) {
                /*
                if (typeof col.id === 'number') {
                    this.deletedCol.push(col.id)
                }
                */
                this.selectedTable.column_collection.splice(this.column_collection.indexOf(col), 1)
            },

            editCol: function (colObject) {
                this.beforeEditCache = {
                    name: colObject.name,
                    label: colObject.label,
                    type: colObject.type,
                    options: colObject.options,
                    nullable: colObject.nullabel,
                    disable: true
                }
                // this.beforeEditCache = colObject
                // this.editedCol = colObject
                colObject.disable = false
                // alert(JSON.stringify(colObject))
            },

            doneEditCol: function (colObject) {
                /*
                if (!this.editedCol) {
                    return
                }
                /*
                this.editedCol = null
                
                /*
                colObject.id = (colObject.name === typeof(1234) ? colObject.id : null)
                colObject.name = (colObject.name === typeof('string') ? colObject.name.trim() : '')
                colObject.label = (colObject.label === typeof('string') ? colObject.label.trim() : '')
                colObject.type = (colObject.type === typeof('string') ? colObject.type.trim() : 'text')
                colObject.options = (colObject.options === typeof('string') ? colObject.options.trim() : '')
                colObject.nullable = (colObject.nullable === typeof(true) ? colObject.nullable : true)
                
                
                if (!colObject.name | !colObject.label) {
                    this.removeCol(colObject)
                }
                */
                colObject.disable = true
            },

            cancelEditCol: function (colObject) {
                // this.editedCol = null
                colObject = this.beforeEditCache
                colObject.disable = true
            },
            
            saveTable() {
                if( this.selectedTable.id === null) {
                    delete this.selectedTable.id
                    this.$store.dispatch('addTable', this.selectedTable)
                } else {
                    // let rowId = this.selectedTable.id
                    this.$store.dispatch('updateTable', this.selectedTable)
                }
            }

        },

        // a custom directive to wait for the DOM to be updated
        // before focusing on the input field.
        // http://vuejs.org/guide/custom-directive.html
        directives: {
            'col-focus': function (el, binding) {
                if (binding.value) {
                    el.focus()
                }
            }
        }
    }
</script>

<style>
</style>


