<template>
    <div >
        <data-tables-server :data="data" :total="total" :loading="loading" @query-change="queryChange" :action-col-def="actionColDef" :table-props="tableProps">
            <el-table-column v-for="title in titles" :prop="title.prop" :label="title.label" :key="title.prop" sortable="custom">
            </el-table-column>
        </data-tables-server>
    </div>
</template>

<script>

import moment from 'moment'
import uniq from 'lodash/uniq'
import orderBy from 'lodash/orderBy'

const typeOf = o => Object.prototype.toString.call(o).slice(8, -1).toLowerCase()
const purify = o => JSON.parse(JSON.stringify(o)) // purify data


export default {
  data() {
    return {
      data: [],
      titles : [{
            prop: "id",
            label: "NO."
        }, {
            prop: "name",
            label: "Name"
        }, {
            prop: "description",
            label: "Description"
      }],
      total: 0,
      loading: false,
      actionColDef: {
        label: 'Actions',
        tableColProps: {
          align: 'center'
        },
        def: [{
          handler: row => {
            // this.$message('Edit clicked ' + row.id)
            let rowId = row.id
            this.$store.dispatch('selectTable', rowId)
            this.$router.push({ name: 'risks', params: { rowId }})
            // this.$message('RUA in row clicked ' + row.flow_no)
          },
          buttonProps: {
            type: 'info'
          },
          name: 'Edit'
        }]
      },
      tableProps: {
        height: 450,
        defaultSort: {
          prop: 'id',
          order: 'descending'
        }
      }
    }
  },
  created() {
    this.mockServer({
      page: 1,
      pageSize: 10
    })
  },
  methods: {
    mockServer( res ) {

        let datas = this.$store.getters.tables.slice()
        let allKeys = Object.keys(this.$store.getters.tables[0])
        // do filter
        res.filters && res.filters.forEach(filter => {
            datas = datas.filter(data => {
                let props = filter.props || allKeys
                return props.some(prop => {
                    if (!filter.vals || filter.vals.length === 0) {
                        return true
                    }
                    return filter.vals.some(val => {
                        return data[prop].toString().toLowerCase().indexOf(val.toLowerCase()) > -1
                    })
                })
            })
        })

        // do sort
        if (res.sortInfo && res.sortInfo.order) {
            let order = res.sortInfo.order
            let prop = res.sortInfo.prop
            let isDescending = order === 'descending'

            datas.sort(function(a, b) {
                if (a[prop] > b[prop]) {
                    return 1
                } else if (a[prop] < b[prop]) {
                    return -1
                } else {
                    return 0
                }
            })
            if (isDescending) {
                datas.reverse()
            }
        }
        /*
        const resus = {
            data: datas.slice((res.page - 1) * res.pageSize, res.page * res.pageSize),
            req: res,
            ts: new Date(),
            total: datas.length
        }
        */
        this.data = datas.slice((res.page - 1) * res.pageSize, res.page * res.pageSize)
        this.total = datas.length
        // return Promise.resolve(resus).reject('network error')
    },
    loadData( info ) {
        this.loading = true
        this.mockServer(info)
        .then(data => {
          this.data = data.data
          this.total = data.total
          this.loading = false
        })
        .catch(error => {
          this.loading = false
        })
        
        // this.data = this.$store.getters.tables.slice((info.page - 1) * info.pageSize, info.page * info.pageSize)
        // this.total = this.$store.getters.tables.length
        this.loading = false
    },
    queryChange( info ) {
      this.mockServer(info)
    }
  }
}
</script>

<style>
@import url("//unpkg.com/element-ui/lib/theme-chalk/index.css")
</style>


