import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import riskList from '@/components/risksList'
import riskDetails from '@/components/riskDetails'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/risks',
      name: 'List',
      component: riskList
    },
    {
      path: '/risks/:id',
      name: 'Details',
      component: riskDetails
    }
  ]
})



